import React, {Component} from 'react';
import logo from './logo.svg';
import './App.css';
import Menu from "./components/menu/menu"
import Register from "./components/register/Register"
import EntregaEpi from "./components/entrega-epi/EntregaEpi"
import Login from "./components/login/login"

class App extends Component {

  constructor() {
    super();
    this.state = {
      loggedIn: false,
      user: {
        id: 0,
      }
    }
  }

  handleOnSignIn = (id) => {
    this.setState({
      user: {
        id
      }
    }
    );

    this.setState({ loggedIn: true });
  }



  render() {

    // return <SelectPage />
    if (this.state.loggedIn) {
      // return <Drawer />;
      return <div>
        <Menu />
        <EntregaEpi />
      </div>;
    }
    else {
      return <Login handleOnSignIn={this.handleOnSignIn} />;
    }

  }
}

export default App;

