import React, { useState, Fragment } from 'react';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import TextField from '@material-ui/core/TextField';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';

import { KeyboardDatePicker } from '@material-ui/pickers/';
// import { MuiPickersUtilsProvider, InlineDatePicker } from "material-ui-pickers";

import 'date-fns';

import InputLabel from '@material-ui/core/InputLabel';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';

import CampoFuncionario from "../campo-funcionario/CampoFuncionario"


const useStyles = makeStyles(theme => ({
    '@global': {
        body: {
            backgroundColor: theme.palette.common.white,
        },
    },
    paper: {
        marginTop: theme.spacing(8),
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
    },
    avatar: {
        margin: theme.spacing(1),
        backgroundColor: theme.palette.secondary.main,
    },
    form: {
        width: '100%', // Fix IE 11 issue.
        marginTop: theme.spacing(3),
    },
    submit: {
        margin: theme.spacing(3, 0, 2),
    },
    formControl: {
        margin: theme.spacing(0),
        width: '100%',
    },
}));


export default function SignUp() {
    const classes = useStyles();

    const inputLabel = React.useRef(null);
    const [labelWidth, setLabelWidth] = React.useState(0);
    React.useEffect(() => {
        setLabelWidth(inputLabel.current.offsetWidth);
    }, []);

    const [state, setState] = React.useState({
        tipoEpi: '',
        subTipoEpi: '',
    });


    const handleChange = name => event => {
        setState({
            ...state,
            [name]: event.target.value,
        });
    };

    const subTipo = () => {
        if (state.tipoEpi === "1") {
            return (
                <Fragment>
                    <option value="" />
                    <option value={1}>afdsaf</option>
                    <option value={2}>fdsafds</option>
                    <option value={3}>fdsafdsa</option>
                    <option value={4}>fdsafds</option>
                    <option value={5}>fdsaf</option>
                    <option value={6}>fsdafsd</option>
                    <option value={7}>1</option>
                    <option value={8}>1</option>
                </Fragment>
            )
        }
        else {
            return (
                <Fragment>
                    <option value="" />
                </Fragment>)
        }
    }

    return (
        <Container component="main" maxWidth="xs">
            <CssBaseline />
            <div className={classes.paper}>
                <Typography component="h1" variant="h5">
                    Entrega de EPI
        </Typography>
                <form className={classes.form} noValidate>
                    <Grid container spacing={2}>
                        <Grid item xs={12}>
                            <FormControl
                                variant={"outlined"}
                                className={classes.formControl}
                                required
                                fullWidth
                                margin="normal"
                                autoComplete="fname">
                                <InputLabel ref={inputLabel} >
                                    Tipo EPI
                                  </InputLabel>
                                <Select
                                    native
                                    value={state.tipoEpi}
                                    onChange={handleChange('tipoEpi')}
                                    labelWidth={labelWidth}
                                    inputProps={{
                                        id: 'tipoEpi',
                                    }}>
                                    <option value="" />
                                    <option value={1}>PROTEÇÃO DA CABEÇA</option>
                                    <option value={2}>PROTEÇÃO DOS OLHOS E FACE</option>
                                    <option value={3}>PROTEÇÃO RESPIRATÓRIA</option>
                                    <option value={4}>PROTEÇÃO DO TRONCO</option>
                                    <option value={5}>PROTEÇÃO DOS MEMBROS SUPERIORES</option>
                                    <option value={6}>PROTEÇÃO DOS MEMBROS INFERIORES</option>
                                    <option value={7}>PROTEÇÃO DO CORPO INTEIRO</option>
                                    <option value={8}>PROTEÇÃO CONTRA QUEDAS COM DIFERENÇA DE NÍVEL</option>
                                </Select>
                            </FormControl>
                        </Grid>
                        <Grid item xs={12}>
                            <FormControl variant="outlined" className={classes.formControl} required>
                                <InputLabel ref={inputLabel}>
                                    Nome EPI
                                  </InputLabel>
                                <Select
                                    native
                                    value={state.subTipoEpi}
                                    onChange={handleChange('subTipoEpi')}
                                    labelWidth={labelWidth}
                                    inputProps={{
                                        id: 'subTipoEpi',
                                    }}>
                                    {subTipo()}
                                </Select>
                            </FormControl>
                        </Grid>
                        <Grid item xs={12} >
                            <TextField
                                autoComplete="fname"
                                variant="outlined"
                                required
                                fullWidth
                                id="firstName"
                                label="Quantidade"
                            />
                        </Grid>
                        <Grid item xs={12} >
                            <TextField
                                id="motivo"
                                required
                                fullWidth
                                label="Motivo"
                                multiline
                                rowsMax="10"
                                rows = "3"
                                className={classes.textField}
                                variant="outlined"
                            />
                        </Grid>
                        <Grid item xs={12}>
                            <CampoFuncionario />
                        </Grid>
                        {/* <Grid item xs={12}>
                            <TextField
                                variant="outlined"
                                required
                                fullWidth
                                id="email"
                                label="Cargo"
                                name="email"
                                autoComplete="email"
                            />
                        </Grid> */}
                        {/* <Grid item xs={12}>
                            <input id="date" type="date" />
                        </Grid>
                        <Grid item xs={12}>
                            <FormControlLabel
                                control={<Checkbox value="isAdmin" color="primary" />}
                                label="Autorizdor."
                            />
                        </Grid> */}
                    </Grid>
                    <Button
                        type="submit"
                        fullWidth
                        variant="contained"
                        color="primary"
                        className={classes.submit}
                    >
                        Salvar
          </Button>

                </form>
            </div>

        </Container>
    );
}