import React, {useEffect} from 'react';
import Select from 'react-select';
import { emphasize, makeStyles, useTheme } from '@material-ui/core/styles';

const useStyles = makeStyles(theme => ({
  root:{
  },
  select: {
    fontSize: "1.2em",
  }
}));



export default function CampoFuncionario() {


  const [companiesList, setList] = React.useState([]);

 

  const classes = useStyles();
  const theme = useTheme();
  const [single, setSingle] = React.useState(null);

  const handleChangeSingle = value => {
    setSingle(value);
  };

 

  const funcionarios = () => {
    return fetch('http://167.71.249.122/funcionarios')
      .then(response => response.json())
      .then(({ data }) => {
        return data.map(funcionario => ({
          value: `${funcionario.funcionario_id}`,
          label: `${funcionario.matricula} - ${funcionario.nome}`,
        }))
      })
      .then(list => {
        console.log(list)
        setList(list)
      })
  }

  useEffect(() => {
    funcionarios();
  }, []);



  return (
    <div className={classes.root}>
        <Select className={classes.select}
        maxMenuHeight={190}
         theme={(theme) => ({
          ...theme,
          borderRadius: 5,
          colors: {
          ...theme.colors,
            primary25: '#D8D6D9',
            primary: '#3f51b5'
          },
          spacing: {
            baseUnit: 8.5,
            // controlHeight: 0,
            // menuGutter: 10
            }
        })}

          TextFieldProps={{
            label: 'Funcionario'
          }}
          placeholder="Selecionar Funcionário"
          options={companiesList}
          onChange={handleChangeSingle}
        />
    </div>
  );
}